.navigation {
  height: 65px;
  width: 100%/5;
  display: flex;
  align-items: center;
  position: relative;
  padding: 2.5rem 4rem;
  background-color: rgb(133, 184, 137);
  color: black;
  box-shadow: 2 2px 2px 2px rgba(9, 9, 9, 0.23);
  font-size: 23px;
  font-weight: 500;
}

.brand-name {
  text-decoration: none;
  color: black;
  font-size: 23px;
  font-weight: 500;
  margin-left: 3rem;

}

.navigation-menu {
  margin-left: auto;
}

.navigation-menu ul {
  display: flex;
  padding: 6px;
}

.navigation-menu li {

  list-style-type: none;
  margin: 0;
  width: 100px;
  flex: 1;
}

.navigation-menu li a {
  text-decoration: none;
  display: block;
  width: 250%/5;
  padding: 16px;
}

.hamburger {
  border: 0;
  height: 40px;
  width: 40px;
  padding: 0.5rem;
  border-radius: 50%;
  background-color: slategrey;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  position: absolute;
  top: 50%;
  right: 25px;
  transform: translateY(-50%);
  display: none;

}

.hamburger:hover {
  background-color: slategray;
}

@media screen and (max-width: 900px) {
  .hamburger {
    display: block;
  }
}

@media screen and (max-width: 900px) {
  .navigation-menu ul {
    display: none;
  }
}

@media screen and (max-width: 900px) {
  .navigation-menu ul {
    position: absolute;
    top: 60px;
    left: 0;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 77px);
    background-color: rgb(255, 246, 225);
    border-top: 1px solid black;
  }
}

.navigation-menu li {
  text-align: center;
  margin: 0;
  padding: 3px;
}

.navigation-menu li a {
  color: black;
  width: 100%;
  padding: 1.5rem 0;
}

.navigation-menu li:hover {
  background-color: rgb(255, 244, 229);
}

@media screen and (max-width: 900px) {
  .navigation-menu ul {
    /* previous styles */
    display: none;
  }
}

@media screen and (max-width: 900px) {
  .navigation-menu.expanded ul {
    display: block;
  }
}